	methods: {
		yiQiangDan:function(){
			let that=this;
				let usurl=that.url1+"/dinghai/order/selectMyOrder";
				// uni.showLoading({  mask: false,   title: '正在抢单'  });
				uni.request({
					url: usurl,
					header:{
						"Content-Type" : "application/x-www-form-urlencoded"
					},
					method:'POST',
					data: {
						useruid:that.user.uid
					},
			    success: (res) => {
					that.list=res.data.flag
					console.log(res)
					
					console.log(that.list)
			    },
				fail : function(err){
					uni.showToast({
						title:"网咯错误",
						icon:"none"
					});
				}
			});
		},
